.comp-rocket{bottom:430px;cursor:pointer}.comp-rocket_body{position:absolute;z-index:99;width:145px;height:336px;background-image:url(/build/static/media/rocket.3dcb40cd.png);background-size:contain}.comp-rocket_body.ready::before{content:'';display:block;width:42px;height:42px;background-image:url(data:image/png;base64,);background-size:contain;position:absolute;left:0;right:0;bottom:0;margin:auto;-webkit-animation:readyCloudLeft 4s linear 1s infinite forwards;animation:readyCloudLeft 4s linear 1s infinite forwards}.comp-rocket_body.ready::after{content:'';display:block;width:42px;height:42px;background-image:url(data:image/png;base64,);background-size:contain;position:absolute;left:0;right:0;bottom:0;margin:auto;-webkit-animation:readyCloudRight 3s linear 2s infinite forwards;animation:readyCloudRight 3s linear 2s infinite forwards}.comp-rocket_body.flight::before{content:'';display:block;width:98px;height:44px;background-image:url(data:image/png;base64,);background-size:contain;position:absolute;bottom:0;left:0;right:0;margin:auto;-webkit-animation:flightCloud .75s ease-out infinite;animation:flightCloud .75s ease-out infinite}.comp-rocket_astronaut{z-index:9;position:absolute;top:50px;left:25px;width:150px;height:150px;margin-left:-30px;background-image:url(/build/static/media/astronaut.3aee6e8c.png);background-size:contain;transition:all 1s linear;transform:translateX(0)}.comp-rocket_astronaut.out-tank{margin-left:100px;-webkit-animation:astronautFloat 5s linear 1.5s infinite alternate;animation:astronautFloat 5s linear 1.5s infinite alternate}@-webkit-keyframes astronautFloat{33%{transform:rotate(-45deg)}66%{transform:rotate(0deg)}100%{transform:rotate(45deg)}}@keyframes astronautFloat{33%{transform:rotate(-45deg)}66%{transform:rotate(0deg)}100%{transform:rotate(45deg)}}@-webkit-keyframes flightCloud{0%{opacity:1;transform:scale(0.5)}100%{opacity:0;transform:translateY(100px) scale(2)}}@keyframes flightCloud{0%{opacity:1;transform:scale(0.5)}100%{opacity:0;transform:translateY(100px) scale(2)}}@-webkit-keyframes readyCloudLeft{0%{opacity:1}20%{opacity:1;transform:translate(-50px, -40px) rotate(160deg)}100%{opacity:0;transform:translate(-100px, -220px) rotate(360deg) scale(2)}}@keyframes readyCloudLeft{0%{opacity:1}20%{opacity:1;transform:translate(-50px, -40px) rotate(160deg)}100%{opacity:0;transform:translate(-100px, -220px) rotate(360deg) scale(2)}}@-webkit-keyframes readyCloudRight{0%{opacity:1}20%{opacity:1;transform:translate(60px, -50px) rotate(-160deg)}100%{opacity:0;transform:translate(120px, -240px) rotate(-360deg) scale(2)}}@keyframes readyCloudRight{0%{opacity:1}20%{opacity:1;transform:translate(60px, -50px) rotate(-160deg)}100%{opacity:0;transform:translate(120px, -240px) rotate(-360deg) scale(2)}}

